@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{  
  min-height: 100vh;   
}

.App {
  display: flex;  
  justify-content: center;
  align-items: center;
  width: 100vw;
  min-height: 100vh; 
  overflow: hidden;
  position: relative;        
}

.App__container{   
  display: flex;  
  width: 90%;
  height: 80vh;
  background-color: white;
  padding: 20px;  
}

.App__left{  
  width: 50%;
  height: auto;  
}

.App__right{  
  width: 50%;
  overflow-y: auto;
  overflow-x: hidden;       
}

.App__btn-open{
  position: absolute;
  right: 1%;
  bottom: 3%;
  font-size: 30px;
  cursor: pointer;
  z-index: 1;
}

.App__btn-close{
  position: absolute;
  right: 1%;
  bottom: 3%;
  font-size: 30px;
  cursor: pointer;
  z-index: 1;
}

.form__container{
    width: 100%;
    height: 100%;    
    display: flex;
    flex-direction: column; 
    justify-content: space-between;      
    align-items: center;
    font-family: 'Quicksand', sans-serif;
    border-right: 1px solid lightgray;       
}

.form__container h1{
    font-size: 30px;
    margin: 0 0 20px;      
}

.form{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 60%;
    height: 80px;    
}

.form__box{
    display: flex;
    justify-content: space-between;
}

.form__box input{
    width: 90%;    
    font-size: 15px;
    border: 0;
    border-bottom: 1px solid lightgray;
    outline: none;    
}


.form__box input::-webkit-input-placeholder{
    color: lightgray
}


.form__box input:-ms-input-placeholder{
    color: lightgray
}


.form__box input::-ms-input-placeholder{
    color: lightgray
}


.form__box input::placeholder{
    color: lightgray
}


.form__title-icon, .form__description-icon{
    font-size: 30px;      
}

.form__btn, 
.form__btn-color{
    all: unset;
    cursor: pointer;
}

.form__btn-color{
    margin: 10px 0;
}

.form__btn:disabled{
    color: lightgray;
    cursor: not-allowed;
}

.form__title-msg, 
.form__description-msg{
    font-size: 12px;
    color: red;
    margin-left: 40px;    
}




.react-calendar, .react-calendar__navigation__label{
    font-family: 'Quicksand', sans-serif !important;          
}

.react-calendar{           
    border: none !important;    
    margin-top: 20px !important;       
}

.react-calendar__navigation{
    margin: 0 !important;
}

.react-calendar__navigation__label, 
.react-calendar__navigation__arrow{
    text-align: center;
    height: 30px !important;
}

.react-time-picker__wrapper{
    border: none !important; 
    margin: 20px !important;   
    text-align: center;    
}

.alarm__container{
    width: 60%;
    min-height: 45vh;
    margin-bottom: 10px;
    display: flex;    
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.alarm__message{
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 55px;
    justify-content: space-between;
    align-items: center;    
}

.alarm__message p{
    margin: 0;    
    font-size: 14px;
}

 .alarm__icon-close{
    font-size: 20px;
    cursor: pointer;
}

.alarm__icon-check{    
    all: unset;
    cursor: pointer;
    font-size: 21px;    
}

.alarm__icon-check:disabled{
    color: lightgray;
    cursor: not-allowed;
}

.alarm__icon-check{
    color:green;
    margin-right: 10px;
}

.alarm__icon-close{
    color: red;
}

.reminder__container{    
    width: 100%;        
    display: flex;
    flex-direction: column;    
    align-items: center;
    font-family: 'Quicksand', sans-serif;        
}

.reminder__container h1{
    font-family: 'Quicksand', sans-serif;
    font-size: 30px;
    margin: 0 0 20px;
}

.reminder__btn{
    all: unset;
    width: 25px;
    height: 25px;
    border-radius: 25px;
    display: flex;    
    justify-content: center;
    align-items: center;   
    margin-left: auto;
    cursor: pointer;
}

.reminder__alarms{
    display: block;
}

.reminder__alarm-btn,
.reminder__update,
.reminder__cancel{
    all: unset;
    cursor: pointer;
    margin-left: 10px;
    font-size: 12px;
}

.reminder__title{ 
    display: inline-block;   
    font-size: 20px;
    font-weight: 800;
    cursor: pointer;   
}

.reminder__description{       
    font-size: 15px;
    cursor: pointer;    
}

.reminder__title:hover, 
.reminder__description:hover{
    text-decoration: underline;
    
}

.reminder__box{
    width: 35vw;
    min-height: 120px;
    padding: 10px;    
    border: 1px solid;
    margin-bottom: 10px;
    border-color: lightgrey;             
}








.notification__container{
    position: absolute;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    top: 0;    
    right: 0;   
    width: 30%;
    height: 80px;
    padding: 2px;           
}

.notification{      
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 250px;
    height: 100%;    
    border-radius: 5px;       
    background-color: paleturquoise;
    margin: 0 5px;
    padding: 0 15px;       
}


.notification__btn{
    all: unset;    
    color: black;
    display: flex;    
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
